.condition {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.condition-icon_box {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.condition-icon {
  font-size: 1.5rem;
}

.condition-field {
  font-size: 1.2rem;
}

.condition-data {
  font-size: 2.2rem;
}

@media only screen and (max-width: 768px) {
  .condition-icon {
    font-size: 1.2rem;
  }
  .condition-field {
    font-size: 1rem;
  }
  .condition-data {
    font-size: 1.8rem;
  }
}
@media only screen and (max-width: 576px) {
  .condition-icon {
    font-size: 1rem;
  }
  .condition-field {
    font-size: .8rem;
  }
  .condition-data {
    font-size: 1.3rem;
  }
}